import { Cargo } from "./Cargo"
import { Astronaut } from "./Astronaut";
import { Payload } from "./Payload";

export class Rocket {
    name: string;
    totalCapacityKg: number;
    cargoItems: Array<Cargo> = [];
    astronauts: Array<Astronaut> = [];
    
    constructor(name: string, totalCapacitKg: number) {
        this.name = name;
        this.totalCapacityKg = totalCapacitKg;
    }

    sumMass(items: Array<Payload>): number {
        let totalWeight: number = 0;
        for (let item of items) {
            totalWeight += item.massKg;
        }
        return totalWeight;
    }

    currentMassKg(): number {
        return this.sumMass(this.cargoItems) + this.sumMass(this.astronauts);
    }

    canAdd(item: Payload): boolean {
        return this.currentMassKg() + item.massKg <= this.totalCapacityKg;
    }

    addCargo(cargo: Cargo): boolean {
        if (this.canAdd(cargo)) {
            this.cargoItems.push(cargo);
        }
        return this.canAdd(cargo);
    }

    addAstronaut(astronaut: Astronaut): boolean {
        if (this.canAdd(astronaut)) {
            this.astronauts.push(astronaut);
        }
        return this.canAdd(astronaut);
    }
}